<template>
  <div class="post">
    <div class="post-header">
      <div class="profile" :style="{ backgroundImage : 'url(' +  instaValue.userImage + ')' }"></div>
      <span class="profile-name">{{ instaValue.name }}</span>
    </div>
    <div :class=instaValue.filter class="post-body" @click="$store.commit('likePost', idx)" :style="{ backgroundImage : 'url(' + instaValue.postImage + ')' }"></div>
    <div class="post-content">
      <p>{{ $store.state.likes[idx] }} Likes</p>
      <p><strong>{{ instaValue.filter }}</strong> {{ instaValue.content }}</p>
      <p class="date">{{ instaValue.date }}</p>
    </div>
</div> 
</template>

<script>
export default {
    name: 'PostVue',
    props : {
        instaValue: Object,
        idx: Number,
    }
}
</script>

