<template>
  <div class="header">
    <ul class="header-button-left">
      <li @click="cancleButton">Cancel</li>
    </ul>
    <ul class="header-button-right">
      <li v-if="tapState==1" @click="tapState=2">Next</li>
      <li v-if="tapState==2" @click="publish">발행</li>
    </ul>
    <img src="https://photoshare-ps.s3.ap-northeast-2.amazonaws.com/ProjectFiles/KakaoTalk_20231023_155712928.png" class="logo" />
  </div>

  <button @click="downloadSelectedFiles">Download Selected Files</button>

  <div class="scrollable-container" ref="scrollContainer">
    <div class="content image-grid">
      <div v-for="(image, index) in getImageData" :key="index" class="image-item">
        <input
        type="checkbox"
            :id="'checkbox-' + index"
            :checked="selectedImages.includes(index)"
            @change="toggleImageSelection(index)"
            class="checkbox"
            v-if="checkBoxState==true"
        />
        <label :for="'checkbox-' + index">
        <img :src="image.mediaUrl" class="fixed-height-image"  
        @contextmenu.prevent="handleShowCheckbox(index)" 
        @touchstart.prevent="handleStartImageSelection(index)"
        @touchmove.prevent="handleSelectImage($event)"
        @touchend.prevent="handleEndImageSelection"
        />
        </label>
      </div>
    </div>
  </div>
  

  <ContainerVue :uploadImg="uploadImg" :tapState="tapState" :instaData="instaData" @write="writeContent=$event"/>
  <div class="footer">
    <ul class="footer-button-plus">
      <!-- accept는 이미지만 보이게 하는 거고 억제하려면 파일 type(확장자)을 확인해야 한다.-->
      <input @change="uploadServer" accept="image/*" multiple ref="fileInput" type="file" id="file" class="inputfile" />
      <label for="file" class="input-p.lus">+</label>
    </ul>
 </div>
 



</template>

<script>

import ContainerVue from './components/ContainerVue.vue';
import instaData from './assets/InstaData.js';
import axios from 'axios';
import JSZip from 'jszip';

export default {
  name: 'App',
  data() {
    return {
      instaData : instaData,
      cnt : 0,
      buttonState : 0,
      tapState: 3,
      uploadImg : [],
      getImageData : [],
      selectedImages: [], // 선택된 이미지의 인덱스를 저장
      currentSelectedImages: [], // 현재 선택된 인덱스 저장
      showCheckboxes: [], // 이미지마다 체크박스를 표시할지 여부를 저장
      writeContent: '',
      selectFilter: '',
      timeCounter: 0,
      touchY: 0,
      checkBoxState : true,
      isSelecting: false, // 이미지 선택 중 여부를 저장
      startSelectionIndex: null, // 선택 시작 이미지의 인덱스를 저장
      startScrollTop: null,
    }
  },
  beforeMount() {
    // 데이터를 가져오는 Axios 요청
    axios.get('https://api.jinyeong.site/api/v1/media')
      .then(response => {
        this.getImageData = response.data; // 받은 데이터를 Vue 데이터에 할당
      })
      .catch(error => {
        console.error('Error fetching data:', error);
      });
  },
  mounted() {
    // 스크롤 가능한 컨테이너 엘리먼트
    const scrollContainer = this.$refs.scrollContainer;

    let isScrolling = false;

    // touchstart 이벤트 리스너
    scrollContainer.addEventListener('touchstart', () => {
      isScrolling = false;
    });

    // touchmove 이벤트 리스너
    scrollContainer.addEventListener('touchmove', (event) => {
      const { clientY } = event.touches[0];
      if (clientY > 600) {
        isScrolling = true;
        // 스크롤을 아래로 내립니다.
        scrollContainer.scrollTop += 10; // 조정 가능한 값
        // 다른 작업을 수행합니다.
        // 여기에 원하는 작업을 추가하세요.
      }
    });

    // touchend 이벤트 리스너
    scrollContainer.addEventListener('touchend', () => {
      isScrolling = false;
    });

    // 주기적으로 스크롤 위치를 확인하고 조정
    setInterval(() => {
      if (isScrolling == true && this.touchY > 600) {
        isScrolling = true;
        // 스크롤을 아래로 내립니다.
        scrollContainer.scrollTop += 30; // 조정 가능한 값
        // 다른 작업을 수행합니다.
        // 여기에 원하는 작업을 추가하세요.
      }
    }, 500); // 1초마다 스크롤 위치 확인 및 조정
  },
  components: {
    ContainerVue: ContainerVue,
  },
  methods : {
    handleStartImageSelection(index) {
      this.isSelecting = true;
      this.startSelectionIndex = index;
      this.toggleImageSelection(index);
    },
    handleSelectImage(event) {
      if (this.isSelecting && this.startSelectionIndex !== null) {
        
        event.preventDefault(); // 터치 이벤트의 기본 동작을 막아 스크롤 방향을 제어합니다.
        // 현재 터치 이벤트의 좌표 가져오기
        const touchX = event.touches[0].clientX;
        this.touchY = event.touches[0].clientY;
        // console.log(touchY);
        // if (touchY > 600) {
        //   this.$refs.scrollContainer.scrollTop += 10;
        // }
        
        
        // 초기 터치 이벤트의 좌표 가져오기
        const startTouchX = event.targetTouches[0].clientX; // 혹은 저장된 좌표를 가져와서 사용
        const startTouchY = event.targetTouches[0].clientY; // 혹은 저장된 좌표를 가져와서 사용
        
        // 이 좌표를 이용하여 어떤 이미지가 선택되었는지 판단
        const selectedImageIndex = this.getSelectedImageIndex(startTouchX, startTouchY, touchX, this.touchY);
        
        if (!this.currentSelectedImages.includes(selectedImageIndex)) {
          // 선택된 이미지의 인덱스를 사용하여 이미지 선택 토글
          this.toggleImageSelection(selectedImageIndex);
        }
        
      }
    },
    handleEndImageSelection() {
      this.isSelecting = false;
      this.startSelectionIndex = null;
      this.currentSelectedImages = [];
    },
    getSelectedImageIndex(startX, startY, currentX, currentY) {
      // startX, startY는 초기 터치 위치, currentX, currentY는 현재 터치 위치
      // 이 좌표를 이용하여 선택된 이미지의 인덱스 계산
      // 선택된 이미지의 인덱스를 반환
      const imageItems = document.querySelectorAll('.image-item');
      for (let i = 0; i < imageItems.length; i++) {
        const rect = imageItems[i].getBoundingClientRect();
        if (
          currentX >= rect.left && currentX <= rect.right &&
          currentY >= rect.top && currentY <= rect.bottom
        ) {
          return i;
        }
      }
      return -1; // 선택된 이미지 없음
    },
    cancleButton() {
      if (this.checkBoxState == true) {
        this.checkBoxState = false;
        this.selectedImages = [];
      }
    },
    handleShowCheckbox(index) {
      console.log('showCheckbox')
      this.showCheckboxes[index] = true;
      this.checkBoxState = true;
    },
    hideCheckbox(index) {
      this.showCheckboxes[index] = false;
    },
    toggleImageSelection(index) {
      console.log('toggleImage: ' + index);
      if (this.currentSelectedImages.includes(index)) {
        this.currentSelectedImages = this.selectedImages.filter((i) => i !== index);
      } else {
        this.currentSelectedImages.push(index);
      }
      // 이미지 선택/해제 토글
      if (this.selectedImages.includes(index)) {
        this.selectedImages = this.selectedImages.filter((i) => i !== index);
        
      } else {
        this.selectedImages.push(index);
      }
    },
    async uploadServer() {

      const formData = new FormData();

      const files = this.$refs.fileInput.files;

      for (let i = 0; i < files.length; i++) {
        formData.append("file", files[i]);
      }

      console.log(formData);
      await axios.post('https://api.jinyeong.site/api/v1/media?file', formData, {
          headers: {
            "Content-Type": "multipart/form-data",
          },
        })
      .then((response) =>{
        if (this.checkBoxState) {
          this.checkBoxState = false;
          this.selectedImages = [];
        }
        for (let i=0; i<response.data.length; i++) {
          this.getImageData.unshift(response.data[i]);
        }  
      })
      .catch(erro => {
        console.log(erro);
      })
      .then(() => {
        console.log("execute");
      })
    },
    async downloadSelectedFiles() {
      // if (this.selectedFiles.length === 0) {
      //   // 선택한 파일이 없다면 처리 중단
      //   return;
      // }

      // // 선택한 파일들의 이름을 추출하여 서버로 보내줌
      // const fileNames = this.selectedFiles.map((file) => file.name);
      
      try {
     // 서버로 선택한 파일들의 이름을 보내서 zip 파일을 다운로드 받음
      const requestDownloadFiles = {
      "fileNames": ["00cd01a8-e359-4d59-819b-cf27300c902e.png", "01b59553-5930-48fa-961d-74151905164b.png", "0c53672e-b946-455d-a324-41de937ef10f.png"]
    };

      const response = await axios.post('https://api.jinyeong.site/api/v1/media/download', JSON.stringify(requestDownloadFiles), {
        responseType: 'blob',
        headers: {
          'Content-Type': 'application/json'
        }
      });

      const zipBlob = await new Blob([response.data]);
      console.log(zipBlob)
      const zip = new JSZip();

      await zip.loadAsync(zipBlob);

        // 압축 해제된 파일들을 사용자에게 제공
      zip.forEach(async (relativePath, file) => {
          const fileData = await file.async('blob');
          const downloadLink = document.createElement('a');
          downloadLink.href = URL.createObjectURL(fileData);
          downloadLink.download = relativePath;
          downloadLink.textContent = `Download ${relativePath}`;
          downloadLink.click();
      });
    } catch (error) {
      console.error('Error downloading files', error);
    }
    },
    more() {
      // axios.post('URL', {name : 'kim'}).then().catch()
        axios.get('https://codingapple1.github.io/vue//more' + this.cnt + '.json')
        .then((result)=>{
          this.instaData.push(result.data);
          this.cnt = 1;
        })
      
      // .catch((err)=>{
      //   alert(err);
      // })
    },
    clickTab(num) {
      this.buttonState = num
    },
    publish() {
      var myPost = {
        name: "Kim Hyun",
        userImage: "https://picsum.photos/100?random=3",
        postImage: this.uploadImg[0],
        likes: 36,
        date: "May 15",
        liked: false,
        content: this.writeContent,
        filter: this.selectFilter
      };
      this.instaData.unshift(myPost);
      this.tapState = 0;
      console.log(myPost);
    }
  }
}
</script>

