<template>
  <div :class="instaFilter" class="filter-item" @click="clickFilter(instaFilter)" :style="{backgroundImage:'url('+ uploadImg[0] +')'}">
    <slot name="a"></slot>
    <slot name="b"></slot>
    <button @click="fire">버튼</button>
  </div> 
</template>

<script>
export default {
    name: 'FilterBox',
    props: {
        uploadImg: Array,
        instaFilter: String,
    },
    methods: {
      fire() {
        this.emitter.emit('작명', '데이터')
      },
      clickFilter(filterName) {
        this.emitter.emit('filterName', filterName)
      }
    },
}
</script>

<style>
.filter-item {
  width: 100px;
  height: 100px;
  margin: 10px 10px 10px auto;
  padding: 8px;
  display: inline-block;
  color : white;
  background-size: cover;
  background-position : center;
}
</style>