<template>
  <div>
    <div v-for="(instaValue, i) in instaData" :key="i">
        <PostVue v-if="tapState == 0" :idx="i" :instaValue="instaData[i]"/>
    </div>

    <!-- 필터선택페이지 -->
    <div v-if="tapState == 1">
        <div :class=imgFilter class="upload-image" :style="{backgroundImage : 'url(' + uploadImg[0] + ')' }" ></div>
        <div class="filters">
            <!-- <div v-for="(instaFilter,i) in instaFilters" :key="i"> -->
            <FilterBoxVue :uploadImg="uploadImg" v-for="(instaFilter, i) in instaFilters" :key="i" :instaFilter="instaFilter">
                <template v-slot:a>{{ instaFilter }}</template>
                <template v-slot:b> <span> img</span></template>
            </FilterBoxVue>
            <!-- </div> -->
        </div>
        <!-- <div class="filters" v-for="(img, i) in uploadImg" :key="i">
            <div class="filter-1" v-if="i<uploadImg.length-1" :style="{backgroundImage : 'url(' + uploadImg[i+1] + ')'}"></div>
        </div> -->
    </div>

    <!-- 글작성페이지 -->
    <div v-if="tapState == 2">
        <div :class=imgFilter class="upload-image" :style="{backgroundImage : 'url(' + uploadImg[0] + ')' }"></div>
        <div class="write" >
            <textarea @input="$emit('write', $event.target.value)" class="write-box">write!</textarea>
        </div>
    </div>

    <div v-if="tapState == 3">
        <!-- <MyPageVue :one="1" /> -->
    </div>
</div>
</template>

<script>

import PostVue from './PostVue.vue';
import FilterBoxVue from './FilterBoxVue.vue';
// import MyPageVue from './MyPageVue.vue';

export default {
    name: 'ContainerVue',
    data() {
        return {
            instaFilters : [ "aden", "_1977", "brannan", "brooklyn", "clarendon", "earlybird", "gingham", "hudson", 
                            "inkwell", "kelvin", "lark", "lofi", "maven", "mayfair", "moon", "nashville", "perpetua", 
                            "reyes", "rise", "slumber", "stinson", "toaster", "valencia", "walden", "willow", "xpro2"],
            imgFilter: '',
        }
    },
    mounted() {
        this.emitter.on('filterName', (a)=>{
            this.imgFilter = a;
        })
    },
    components: {
        PostVue: PostVue,
        FilterBoxVue: FilterBoxVue,
        // MyPageVue: MyPageVue,
    },
    props: {
        instaData: Array,
        tapState: Number,
        uploadImg : Array,
    }
}
</script>

<style>
.upload-image{
width: 100%;
height: 450px;
background: cornflowerblue;
background-size : cover;
}
.filters{
overflow-x:scroll;
white-space: nowrap;
}
.filter-1 {
width: 100px;
height: 100px;
background-color: cornflowerblue;
margin: 10px 10px 10px auto;
padding: 8px;
display: inline-block;
color : white;
background-size: cover;
}
.filters::-webkit-scrollbar {
height: 5px;
}
.filters::-webkit-scrollbar-track {
background: #f1f1f1; 
}
.filters::-webkit-scrollbar-thumb {
background: #888; 
border-radius: 5px;
}
.filters::-webkit-scrollbar-thumb:hover {
background: #555; 
}
.write-box {
border: none;
width: 90%;
height: 100px;
padding: 15px;
margin: auto;
display: block;
outline: none;
}
</style>