import { createStore } from 'vuex'
import axios from 'axios'

const store = createStore({
  state(){
    return {
      name : 'kim',
      age : 20,
      likes: [30,30,30],
      likeState: [false, false, false],
      more: {},
    }
  },
  mutations: {
    setMore(state, data) {
        state.more = data
    },
    changeName(state) {
        state.name = 'park'
    },
    plusAge(state, num) {
        state.age += num
    },
    likePost(state, idx) {
        if (state.likeState[idx] == false) {
            state.likes[idx] += 1;
            state.likeState[idx] = true;
        } else {
            state.likes[idx] -= 1;
            state.likeState[idx] = false;
        }
        
    }
  },
  actions : {
    getData(context) {
        axios.get('https://codingapple1.github.io/vue//more0.json')
        .then((a)=>{
            context.commit('setMore', a.data)
        })
    },
  }

})

export default store